import { Box, Button, Checkbox, Popover, Stack, Tooltip } from "@mantine/core";
import { EvaluationSet, JSONValue } from "../../types";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";
import { useEffect, useMemo, useRef, useState } from "react";
import parallelCoordinates from "highcharts/modules/parallel-coordinates";
import { parseJson } from "../../utils/jsonUtils";
import { useColorScheme } from "@mantine/hooks";
import { createPortal } from "react-dom";
import { IconListCheck } from "@tabler/icons-react";
import { pickBy } from "lodash";

parallelCoordinates(Highcharts);

type Props = {
  evaluationSets: EvaluationSet[];
};

function refineCategoryValue(value: JSONValue): string {
  if (typeof value === "number") {
    return value.toString();
  }
  if (typeof value === "string") {
    try {
      // Handle any double/single quote weirdness
      const val = parseJson(value);
      return JSON.stringify(val);
    } catch {
      return value;
    }
  }

  try {
    return JSON.stringify(value);
  } catch {
    return "";
  }
}

function getNumberValue(value: JSONValue): number | null {
  if (typeof value === "number") {
    return value;
  }
  if (typeof value === "string") {
    try {
      const parsedNum = JSON.parse(value);
      if (typeof parsedNum === "number") {
        return parsedNum;
      }
    } catch {
      return null;
    }
  }

  return null;
}

export default function ParallelCoordinatesChart({ evaluationSets }: Props) {
  const chartComponentRef = useRef<HighchartsReact.RefObject>(null);

  const { paramCategories, metricCategories, evalSetNames } = useMemo(() => {
    const paramCategories: Record<
      string,
      {
        categoryIdx: number;
        categoryIdxMap: Record<string | number, number>;
      }
    > = {};
    const metricCategories: Record<
      string,
      {
        categoryIdx: number;
        categoryIdxMap: Record<string | number, number>;
      }
    > = {};

    // First pass, just collect info for constructing the x and y-axis categories
    // Create an x-axis category for each unique param and metric name
    // Create a y-axis category for each unique value of each param and metric
    // Track y-axis category index for each unique value to use for the series values
    // For numbers, just use the number value so that the order/scale is correct
    const evalSetNames: string[] = [];
    evaluationSets.forEach((set) => {
      evalSetNames.push(set.name);

      Object.keys(set.paramSet ?? {}).forEach((paramName) => {
        if (!paramCategories[paramName]) {
          paramCategories[paramName] = {
            categoryIdx: 0,
            categoryIdxMap: {},
          };
        }

        const currentParamCategoryValue =
          getNumberValue(set.paramSet![paramName]) ??
          refineCategoryValue(set.paramSet![paramName]);

        if (
          paramCategories[paramName].categoryIdxMap[
            currentParamCategoryValue
          ] == null
        ) {
          paramCategories[paramName].categoryIdxMap[currentParamCategoryValue] =
            paramCategories[paramName].categoryIdx;
          paramCategories[paramName].categoryIdx++;
        }
      });

      set.evaluationSetMetrics?.forEach((metric) => {
        if (!metricCategories[metric.metricName]) {
          metricCategories[metric.metricName] = {
            categoryIdx: 0,
            categoryIdxMap: {},
          };
        }

        const currentMetricCategoryValue =
          getNumberValue(metric.metricValue) ??
          refineCategoryValue(metric.metricValue);

        if (
          metricCategories[metric.metricName].categoryIdxMap[
            currentMetricCategoryValue
          ] == null
        ) {
          metricCategories[metric.metricName].categoryIdxMap[
            currentMetricCategoryValue
          ] = metricCategories[metric.metricName].categoryIdx;
          metricCategories[metric.metricName].categoryIdx++;
        }
      });
    });

    return { paramCategories, metricCategories, evalSetNames };
  }, [evaluationSets]);

  const xAxisCategories = useMemo(
    () => [
      "Evaluation Run",
      ...Object.keys(paramCategories),
      ...Object.keys(metricCategories),
    ],
    [paramCategories, metricCategories]
  );

  const [xAxisVisibility, setXAxisVisibility] = useState<
    Record<string, boolean>
  >(
    xAxisCategories.reduce((acc, curr) => {
      acc[curr] = true;
      return acc;
    }, {} as Record<string, boolean>)
  );

  // Whenever the visibility changes, update the key to force a re-render of the Highcharts
  const [visibilityKey, setVisibilityKey] = useState(0);

  const visibleXAxisCategories = useMemo(
    () =>
      xAxisCategories.filter((category) => xAxisVisibility[category] !== false),
    [xAxisCategories, xAxisVisibility]
  );

  const visibleParamCategories = useMemo(
    () => pickBy(paramCategories, (_, key) => xAxisVisibility[key]),
    [paramCategories, xAxisVisibility]
  );

  const visibleMetricCategories = useMemo(
    () => pickBy(metricCategories, (_, key) => xAxisVisibility[key]),
    [metricCategories, xAxisVisibility]
  );

  const { yAxisOptions, series } = useMemo(() => {
    // Second pass, go through each eval set and map the value for each category to
    // the appropriate index. Series data therefore represents category indices
    // For numbers, just just the number value so that the order/scale is correct
    const series: Highcharts.SeriesSplineOptions[] = evaluationSets.map(
      (evalSet, i) => {
        const data: (string | number | null)[] = [];
        if (visibleXAxisCategories.includes("Evaluation Run")) {
          data.push(i);
        }

        Object.entries(visibleParamCategories).forEach(
          ([paramName, paramCategoryMap]) => {
            const evalSetParamVal = evalSet.paramSet?.[paramName];
            if (evalSetParamVal == null) {
              data.push(null);
            } else {
              data.push(
                getNumberValue(evalSetParamVal) ??
                  paramCategoryMap.categoryIdxMap[
                    refineCategoryValue(evalSetParamVal)
                  ]
              );
            }
          }
        );

        Object.entries(visibleMetricCategories).forEach(
          ([metricName, metricCategoryMap]) => {
            const evalSetMetric = evalSet.evaluationSetMetrics?.find(
              (metric) => metric.metricName === metricName
            );
            if (evalSetMetric == null) {
              data.push(null);
            } else {
              data.push(
                getNumberValue(evalSetMetric.metricValue) ??
                  metricCategoryMap.categoryIdxMap[
                    refineCategoryValue(evalSetMetric.metricValue)
                  ]
              );
            }
          }
        );

        return {
          type: "spline",
          name: evalSet.name,
          data,
          shadow: false,
          color: "rgba(11, 200, 200, 0.25)",
        };
      }
    );

    const paramsYAxisOptions: Highcharts.YAxisOptions[] = Object.values(
      visibleParamCategories
    ).map((v) =>
      getNumberValue(Object.keys(v.categoryIdxMap)[0]) != null
        ? {}
        : {
            type: "category",
            categories: Object.keys(v.categoryIdxMap).map(refineCategoryValue),
          }
    );

    const metricsYAxisOptions: Highcharts.YAxisOptions[] = Object.values(
      visibleMetricCategories
    ).map((v) =>
      getNumberValue(Object.keys(v.categoryIdxMap)[0]) != null
        ? { softMin: 0.5, softMax: 1 }
        : {
            type: "category",
            categories: Object.keys(v.categoryIdxMap).map(refineCategoryValue),
          }
    );

    const yAxisOptions: Highcharts.YAxisOptions[] = [
      visibleXAxisCategories.includes("Evaluation Run")
        ? {
            type: "category",
            categories: evalSetNames,
          }
        : null,
      ...paramsYAxisOptions,
      ...metricsYAxisOptions,
    ].filter((v) => v != null) as Highcharts.YAxisOptions[];

    return {
      yAxisOptions,
      series,
    };
  }, [
    evalSetNames,
    evaluationSets,
    visibleMetricCategories,
    visibleParamCategories,
    visibleXAxisCategories,
  ]);

  const options: Highcharts.Options = useMemo(
    () => ({
      accessibility: {
        enabled: false,
      },
      credits: {
        enabled: false,
      },
      chart: {
        type: "spline",
        parallelCoordinates: true,
        marginLeft: 0,
        parallelAxes: {
          lineWidth: 2,
        },
      },
      title: {
        text: "",
      },
      plotOptions: {
        series: {
          animation: true,
          marker: {
            enabled: false,
            states: {
              hover: {
                enabled: true,
              },
            },
          },
          states: {
            hover: {
              halo: {
                size: 0,
              },
            },
          },
        },
      },
      tooltip: {
        pointFormat:
          '<span style="color:{point.color}">\u25CF</span>' +
          "{series.name}: <b>{point.formattedValue}</b><br/>",
      },

      xAxis: {
        categories: visibleXAxisCategories,
      },

      yAxis: yAxisOptions,

      series,
    }),
    [series, visibleXAxisCategories, yAxisOptions]
  );

  const preferredColorScheme = useColorScheme();

  const [buttonPortal, setButtonPortal] = useState<HTMLElement | null>(null);
  const [isConfigureMenuOpen, setIsConfigureMenuOpen] = useState(false);

  // Use an effect since portal element is not in the DOM until after render
  useEffect(() => {
    setButtonPortal(document.getElementById("chart-action-button"));
  }, []);

  return (
    <>
      {buttonPortal != null &&
        createPortal(
          <div>
            <Popover
              opened={isConfigureMenuOpen}
              onClose={() => setIsConfigureMenuOpen(false)}
              offset={0}
            >
              <Popover.Target>
                <Tooltip label="Configure Chart" withArrow position="left">
                  <div>
                    <Button
                      variant="default"
                      size="sm"
                      mb="sm"
                      px="sm"
                      onClick={() => setIsConfigureMenuOpen((cur) => !cur)}
                    >
                      <IconListCheck size={14} />
                    </Button>
                  </div>
                </Tooltip>
              </Popover.Target>
              <Popover.Dropdown>
                <Stack spacing="xs">
                  {xAxisCategories.map((category) => (
                    <Checkbox
                      key={category}
                      label={category}
                      checked={xAxisVisibility[category]}
                      onChange={() => {
                        setXAxisVisibility((prev) => ({
                          ...prev,
                          [category]: !(prev[category] ?? true),
                        }));
                        setVisibilityKey((prev) => prev + 1);
                      }}
                    />
                  ))}
                </Stack>
              </Popover.Dropdown>
            </Popover>
          </div>,
          buttonPortal
        )}
      <Box
        h="100%"
        w="100%"
        className={`highcharts-container-theme-${preferredColorScheme}`}
      >
        <HighchartsReact
          highcharts={Highcharts}
          options={options}
          ref={chartComponentRef}
          key={`parallel-coordinates-chart-${visibilityKey}`}
        />
      </Box>
    </>
  );
}
